/* NAVBAR */
#headerId-skills{
    background: #0F2027;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #2C5364, #203A43, #0F2027);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ 
}

/* CONTENT PAGE */
#content-skills{
    /* This is do hide the scroll-bar and not scroll when not needed*/
    overflow: hidden; 
}

#description{   
    background-color: black;
    opacity: 0.8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
}

#grid-skills{
    background: #67B26F;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #4ca2cd, #67B26F);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #4ca2cd, #67B26F); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  
    text-align: center; 
    padding: 5em; 

}

#grid-skills h1{
    font-weight: bold;
}

#skills-socialId i{
    color: white;
    font-size: 5em;
    padding: 8px;

}

#description-skills{   
    background-color: black;
    opacity: 0.8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
}

#description-skills h1{
    color: white;
    font-size: 50px;
    font-weight: bold;
}
#description-skills h3{
    color: white;
    font-size: 30px;
    text-align: justify;
    margin: 2em;
}

#description-skills hr{
    border-top: 5px dotted white;
    width: 90%;
    margin: auto;
}

#socialId-skills p{
    font-weight: bold;
    color:white;
    font-size: 30px;
}

#socialId-skills i{
    color: white;
    font-size: 5em;
    padding: 8px;

}